.recorder-container {
  position: relative;
}

.camera-video {
  display: none;
}
.camera-canvas {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.camera-canvas.active,
.camera-video.active {
  display: block;
}

.icon-open {
  position: fixed;
  width: 100px;
  height: 100px;
  background-image: url(/mr-hat/static/media/camera-shutter.615f825b.png);
  background-size: 100px;
  left: 50%;
  top: 160px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  z-index: 1;
}

.icon-close {
  position: fixed;
  width: 36px;
  height: 36px;
  background-image: url(data:image/png;base64,);
  background-size: 36px;
  top: 40px;
  right: 20px;
  z-index: 1;
}

.icon-capture {
  position: fixed;
  width: 40px;
  height: 30px;
  background-image: url(/mr-hat/static/media/camera-capture.84e4da24.png);
  background-size: 40px;
  top: 46px;
  left: 20px;
  z-index: 1;
}

.feature-info {
  position: fixed;
  max-height: 20px;
  top: 0;
  left: 0;
  background-color: #F7F2A3;
  padding: 5px 10px;
  font-size: 12px;
  line-height: 20px;
  color: #0185BD;
  -webkit-transition: max-height .3s ease-out;
  transition: max-height .3s ease-out;
  overflow: hidden;
  z-index: 1;
}

.feature-info.active {
  max-height: 100px;
}

.feature-info code {
  font-style: italic;
  color: #45484C;
}

.save-btn {
  position: fixed;
  width: 40px;
  bottom: 60px;
  right: 20px;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity .5s ease;
  transition: opacity .5s ease;
}

.save-btn.active {
  opacity: 1;
  visibility: visible;
}

.save-btn.active img {
  width: 100px;
}

.chooser-container {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 360px;
  -webkit-transform: translateY(300px);
          transform: translateY(300px);
  -webkit-transition: -webkit-transform .3s ease-out;
  transition: -webkit-transform .3s ease-out;
  transition: transform .3s ease-out;
  transition: transform .3s ease-out, -webkit-transform .3s ease-out;
  box-shadow: 0 -2px 20px rgba(0, 0, 0, .12);
  background-color: #fff;
  z-index: 1;
}

.chooser-container.expanded {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.chooser-title {
  height: 60px;
  line-height: 60px;
  text-align: center;
}

.deco-list {
  display: flex;
  list-style: none;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 10px;
}

.deco-item {
  display: flex;
  flex: 0 0 75px;
  height: 40px;
  margin: 10px 0 20px;
  justify-content: center;
  align-items: center;
  background-image: url(/mr-hat/static/media/sprite-beards.bc1de142.png);
  background-repeat: no-repeat;
  background-size: 150px;
}

.deco-item.current {
  border-radius: 4px;
  background-color: #dcdcdc;
  -webkit-transition: background-color .3s ease;
  transition: background-color .3s ease;
}
.app-container {
  text-align: center;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff;
  color: #282c34;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  width: 100%;
  max-width: 100%;
}
